/*_____ASSIGNEES_____*/
.projectName {
    font-size: 14px;
    overflow-wrap: break-word;
    word-break: break-word;
    color: rgb(23, 43, 77);
    font-weight: 600;
    text-transform: capitalize;
    mix-blend-mode: normal;
}

.file-attached-ongoing {
    padding-top: 5px;
    padding-bottom: 5px;

    background: #0D47A1;
    /* border-radius: 20px; */
    overflow: visible;

    position: relative;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #FAFBFC;
    mix-blend-mode: normal;
    margin-right: 10px;
    margin-bottom: 10px;
    text-align: center;
    overflow-wrap: break-word;
    word-break: break-word;
    text-transform: capitalize;


}

.remove-file-ongoing {
    position: absolute;
    background: transparent;

    color: white;
    top: -18px;
    right: -19px;
    border-color: transparent;
}

.remove-file-ongoing:hover, .remove-file-ongoing .active, .remove-file-ongoing:focus {
    background: transparent !important;
    box-shadow: none !important;
    border-color: transparent !important;
    outline: none !important;
}

.remove-file-ongoing:focus {
    outline: 0 !important;
    border: transparent !important;
    box-shadow: none !important;
}

